.dropdown-hover .dropdown > .dropdown-menu {
  display: block;
  visibility: hidden;
  opacity: 0;
  min-width: 250px; }

.dropdown-hover .dropdown:hover > .dropdown-menu {
  visibility: visible;
  opacity: 1; }

.dropdown .dropdown-item {
  display: inline-block; }

.dropdown .dropdown-column {
  padding: .5rem 0 .5rem 1rem;
  min-width: 250px; }
  .dropdown .dropdown-column:last-child {
    padding-right: 1rem; }

.dropdown .dropdown-submenu {
  position: relative; }

.dropdown .dropdown-submenu > .dropdown-menu {
  top: 0;
  left: 100%;
  margin-top: -6px;
  margin-left: -1px;
  display: block;
  visibility: hidden;
  opacity: 0; }

.dropdown .dropdown-submenu:hover > .dropdown-menu {
  visibility: visible;
  opacity: 1; }

.dropdown .dropdown-submenu > a:after {
  display: block;
  content: " ";
  float: right;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  border-width: 5px 0 5px 5px;
  border-left-color: #ccc;
  margin-top: 5px;
  margin-right: -10px; }

.dropdown-fade .dropdown > .dropdown-menu {
  transition: visibility 0s, display 0s, opacity .25s;
  transition-delay: .25s, .25s, 0s; }

.dropdown-fade .dropdown:hover > .dropdown-menu {
  transition-delay: 0s, 0s, 0s; }

.dropdown-fade .dropdown .dropdown-submenu > .dropdown-menu {
  transition: visibility 0s, display 0s, opacity .25s;
  transition-delay: .25s, .25s, 0s; }

.dropdown-fade .dropdown .dropdown-submenu:hover > .dropdown-menu {
  transition-delay: 0s, 0s, 0s; }
